import { Component, AfterContentInit } from '@angular/core';
import { GenerateTodayDeal } from 'assets/js/generate-today-deal';
@Component({
  moduleId: module.id,
  selector: 'today-deal',
  templateUrl: 'today-deal.component.html',
  styleUrls: ['today-deal.component.css']
})

export class TodayDealComponent implements AfterContentInit {
  title = 'Today Deal Page';
  constructor() {
  }

  ngAfterContentInit() {
    GenerateTodayDeal();
  }
}



